import React, { Component } from "react";
import { ReactDOM } from "react";
import "../Css/Contacts.css";

class Contacts extends Component {
  nombreref = React.createRef();
  emailref = React.createRef();
  phoneref = React.createRef();
  mesageref = React.createRef();

  state = {
    user: {},
  };
  recibirdatos = (e) => {
    e.preventDefault();
    // ejemplo fetch(`ùrl/comments?nombre=${usario.nombre}&correo=${usario.correo}`)
    alert("Enviando Informacion");
    console.log(this.nombreref.current.value);
    console.log(this.emailref.current.value);
    console.log(this.phoneref.current.value);
    console.log(this.mesageref.current.value);

    var usuario = {
      nombre: this.nombreref.current.value,
      email: this.emailref.current.value,
      phone: this.phoneref.current.value,
      mesage: this.mesageref.current.value,
    };

    this.setState({
      user: usuario,
    });
    e.target.reset();
  };

  render() {
    return (
      <div className="container contact-form" id="container">
        <div className="contact-image">
          <img
            src="https://s3.amazonaws.com/vla.academy/wp-content/uploads/2020/08/13160600/vlamerica-logo.png"
            width="100"
            height="50"
            alt=""
          />
        </div>
        <form onSubmit={this.Recibirdatos}>
          <h3>Drop Us a Message</h3>
          <div className="row">
            <div className="col-md-6">
              <div className="form-group">
                <input
                  type="text"
                  name="Nombre "
                  className="form-control"
                  placeholder="Name *"
                  ref={this.nombreref}
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="Email"
                  className="form-control"
                  placeholder=" Email *"
                  ref={this.emailref}
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="txtPhone"
                  className="form-control"
                  placeholder="Phone Number *"
                  ref={this.phoneref}
                />
              </div>
              <div className="form-group">
                <input
                  type="submit"
                  name="btnSubmit"
                  className="btnContact"
                  value="Send the Information"
                />
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <textarea
                  name="txtMsg"
                  className="form-control"
                  placeholder="Your Message *"
                  ref={this.mesageref}
                ></textarea>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default Contacts;
